
import { defineComponent, ref, toRefs, onMounted, nextTick } from "vue";
import * as echarts from "echarts/core";
import { BarChart, BarSeriesOption } from "echarts/charts";
import { GridComponent, GridComponentOption } from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<BarSeriesOption | GridComponentOption>;

echarts.use([BarChart, GridComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "BarChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { title } = toRefs(props);
    const dialogVisible = ref(false);

    const option: ECOption = {
      xAxis: {
        type: "category",
        data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
      },
      yAxis: {
        type: "value"
      },
      series: [
        {
          type: "bar",
          data: [120, 200, 150, 80, 70, 110, 130]
        }
      ]
    };

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();
      const chart = echarts.init(
        document.getElementById("bar-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
    };

    onMounted(() => {
      const chart = echarts.init(
        document.getElementById("bar-chart-main-" + title.value)!
      );
      chart.setOption(option);
    });

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
