<template>
  <div id="bar-chart-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div class="chart-container">
      <div :id="'bar-chart-main-' + title" class="chart-main"></div>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="50%">
      <div class="dialog-container">
        <div :id="'bar-chart-dialog-' + title" class="chart-dialog"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, onMounted, nextTick } from "vue";
import * as echarts from "echarts/core";
import { BarChart, BarSeriesOption } from "echarts/charts";
import { GridComponent, GridComponentOption } from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<BarSeriesOption | GridComponentOption>;

echarts.use([BarChart, GridComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "BarChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { title } = toRefs(props);
    const dialogVisible = ref(false);

    const option: ECOption = {
      xAxis: {
        type: "category",
        data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
      },
      yAxis: {
        type: "value"
      },
      series: [
        {
          type: "bar",
          data: [120, 200, 150, 80, 70, 110, 130]
        }
      ]
    };

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();
      const chart = echarts.init(
        document.getElementById("bar-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
    };

    onMounted(() => {
      const chart = echarts.init(
        document.getElementById("bar-chart-main-" + title.value)!
      );
      chart.setOption(option);
    });

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#bar-chart-card-component
  width 100%
  background-color white

  .top-container
    display flex
    justify-content space-between
    align-items center
    padding 20px 30px 0
    .title
      font-size $font-size-lg2x
      font-weight 500
    .expand-button
      cursor pointer
      img
        width 20px
        height 20px

  .chart-container
    width 100%
  .dialog-container
    width 100%
  .chart-main
    chart-main()
  .chart-dialog
    chart-dialog()
</style>
